// @import ''

img {
	max-width: 100%; }

* {
 padding: 0;
 margin: 0;
 box-sizing: border-box;
 outline: none; }

.container {
	max-width: 1108px; }

.s-header {
	padding: 45px 100px 60px;
	background-image: url(../img/header/header-bg.jpg);
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat; }

.header {
	&-logo {
	 width: 125px;
	 height: 63px;
	 display: block; }
	&-contacts {
	 color: #2D2B2B; }
	&-title {
	 margin-top: 90px;
	 margin-bottom: 40px;
  font-size: 25px;
  font-weight: 700;
  color: #e17976; }
 &-subtitle {
 	max-width: 790px;
  color: #2D2B2B;
  font-size: 17px;
  font-weight: 500; } }


.s-propositon {
	background-color: #fef6f6; }


.propositon {
	padding: 60px 0 20px;
 &-item {
  background-color: #fefefe;
  border: 2px solid #eeeeee;
  border-radius: 3px;
  padding-bottom: 40px;
  &__image {
   width: 100%;
   position: relative;
   max-width: 100%;
   max-height: 230px;
   margin-bottom: 33px;
   img {
    display: block;
    width: 100%;
    height: 100%;
    background-size: cover; } }
  &__priceStock {
   padding: 8px 10px;
   border: 3px solid #e17976;
   background-color: #f2e3de;
   position: absolute;
   top: 12px;
   right: 12px;
   font-size: 14px;
   font-weight: 700; }
  &__price {
   padding: 8px 10px;
   border: 3px solid transparent;
   background-color: transparent;
   position: absolute;
   top: 57px;
   right: 12px;
   font-size: 16px;
   text-decoration: line-through; }
  &__title {
   font-size: 15px;
   font-weight: 700;
   padding-left: 15px;
   margin-bottom: 30px;
   color: #2f2f2f; }
  &__descr {
   font-size: 15px;
   text-align: justify;
   padding: 0 15px;
   color: #7d7d7d;
   margin-bottom: 35px; }
  &__button {
   display: block;
   padding: 18px 50px;
   background-color: #e17976;
   text-transform: uppercase;
   color: #fdfbfb;
   border-radius: 3px;
   border: 1px solid transparent;
   cursor: pointer; } } }



